//CustomSelect.js
import React, { useState } from 'react';
import Circle from './Circle';
import './index.css';
import flache from './images/flache.png';

const CustomSelect = () => {
  const [showOptions, setShowOptions] = useState(false);
  const [selectedOption, setSelectedOption] = useState('g');

  const handleToggle = () => {
    setShowOptions(prevState => !prevState);
  };

  const handleOptionClick = optionText => {
    setSelectedOption(optionText);
    setShowOptions(false);
  };

  return (
    <div className="custom-select">
      <div className="selected-option" onClick={handleToggle}>
        {selectedOption}
        <button className="a ">
          <Circle radius={12} color="#8DCDFB" src={flache} padding={3} />
        </button>
      </div>
      {showOptions && (
        <div className="options">
          <div className="option" onClick={() => handleOptionClick('g')}>
            g
          </div>
          <div className="option" onClick={() => handleOptionClick('kg')}>
            kg
          </div>
          <div className="option" onClick={() => handleOptionClick('t')}>
            t
          </div>
        </div>
      )}
    </div>
  );
};

export default CustomSelect;
