//affichage.js
import React, { useState } from 'react';
import './App.css';
import Circle from './Circle';
import CustomSelect from './CustomSelect';
import plussss from './images/plussss.png';
import moins from './images/moins.png';
import tomato_bunch from'./images/tomato_bunch.png';
import 'bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './index.css';
import ImageWithCaption from './ImageWithCaption';

const Appl= () => {
  const [value, setValue] = useState(0);

  const incrementValue = () => {
    setValue((prevValue) => (prevValue < 10 ? prevValue + 1 : prevValue));
  };

  const decrementValue = () => {
    setValue((prevValue) => (prevValue > 0 ? prevValue - 1 : prevValue));
  };

  return (
    <div>
      <div className='we'>
        <ImageWithCaption fluid src={tomato_bunch} title="tomato" />
        <Container className='habibi' fluid>
          <Row>
            <Col  className='weeee'>
              <div className='all'>
                <div className='moins'>
                  <button className='a' onClick={decrementValue}>
                    <Circle radius={12} color="#8DCDFB" src={moins} padding={3} />
                  </button>
                </div>
                <input className='zone' type="text" value={value} />
                <div className='plus'>
                  <button className='a' onClick={incrementValue}>
                    <Circle radius={12} color="#8DCDFB" src={plussss} padding={3} />
                  </button>
                </div>
              </div>
            </Col>

            <Col  className='weeeee'>
              <div className='x'><CustomSelect /></div>
            </Col>
          </Row>
        </Container>

        <button className='add-button'>Add to Banner</button>
      </div>
    </div>
  );
};

export default Appl;
