// Circle.js
import React from 'react';

const Circle = ({ radius, color, src, padding }) => {
  const circleStyle = {
    cx: radius,
    cy: radius,
    r: radius,
    fill: color,
  };

  const imageSize = radius * 2 - padding * 2;
  const imageStyle = {
    x: radius - imageSize / 2,
    y: radius - imageSize / 2,
    width: imageSize,
    height: imageSize,
    
  };



  return (
    <div>
    <svg className='circle' height={radius * 2} width={radius * 2}>
      <circle {...circleStyle} />
      <image xlinkHref={src} style={imageStyle} />
    </svg>
    </div>


  );
};

export default Circle;


